html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
*::-webkit-scrollbar {
  display: none;
}
body,
html {
  height: 100%;
  width: 100%;
  font-size: 12px;
  min-width: 320px;
  -webkit-tap-highlight-color: transparent;
  overflow: hidden;
  font-family: "微软雅黑" !important;
}
blockquote,
body,
dd,
dl,
dt,
fieldset,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
html,
iframe,
legend,
li,
ol,
p,
pre,
textarea,
ul {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 400;
}
ul {
  list-style: none;
}
button,
input,
select,
textarea {
  margin: 0;
}
html {
  box-sizing: border-box;
}
*,
:after,
:before {
  box-sizing: inherit;
}
audio,
embed,
iframe,
img,
object,
video {
  height: auto;
  max-width: 100%;
}
.clear,
.clearfix:after {
  height: 0;
  clear: both;
}
iframe {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
  text-align: left;
}
.search {
  height: 28px;
  line-height: 28px;
  text-align: center;
  margin-left: 10px;
  color: red;
}
.red {
  color: red !important;
}
.green {
  color: #179b16 !important;
}
.gold {
  color: #f8a627 !important;
}
.gray {
  color: grey !important;
}
.themeColor {
  color: #1c82d4 !important;
}
.bgRed {
  background-color: red;
}
.p8 {
  padding: 8px;
}
.pt46 {
  padding-top: 46px;
}
.pt86 {
  padding-top: 86px;
}
.pt90 {
  padding-top: 90px;
}
.pb0 {
  padding-bottom: 0 !important;
}
.mgb0 {
  margin-bottom: 0 !important;
}
.mt12 {
  margin-top: 12px;
}
.scrollY {
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
}
.ti2 {
  text-indent: 2em;
}
.tal {
  text-align: left;
}
.fz13 {
  font-size: 13px;
}
.lh40 {
  line-height: 40px;
}
.fz12 {
  font-size: 12px;
}
.back {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  margin: 8px 8px;
  padding: 15px;
}
.back:before {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  border: 1px solid #fff;
  border-width: 1px 0 0 1px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  top: 8px;
  left: 7px;
}
.outBox {
  padding: 0;
}
.borderBottomDash {
  border-bottom: 1px dashed #ccc;
}
.bottomBox {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 15px;
}
.btnBox {
  padding: 15px;
}
.goIn {
  position: relative;
}
.goIn:after {
  content: " ";
  display: inline-block;
  height: 6px;
  width: 6px;
  border-width: 2px 2px 0 0;
  border-color: #C8C8CD;
  border-style: solid;
  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  top: -2px;
  position: absolute;
  top: 50%;
  margin-top: -4px;
  right: 15px;
}
.rightSlot {
  font-size: 16px;
  position: relative;
  top: -3px;
}
.infinite-status-prompt {
  color: #666;
  font-size: 14px;
  text-align: center;
  padding: 10px 0;
}
.loading {
  margin: 0 auto;
  margin-top: 20%;
  width: 80px;
  height: 80px;
}
.container {
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  background-size: contain;
}
.panel {
  margin: 0 8px 12px;
  box-shadow: 0 6px 20px 0 rgba(158, 158, 158, 0.3);
  background: #fff;
  border-radius: 8px;
  padding: 8px;
  position: relative;
}
.panel .title {
  border-left: 2px solid #1c82d4;
  padding-left: 10px;
}
body {
  background-color: #fbf9fe;
}
#app {
  height: 100%;
}



